.widget {
  background: $light-blue;
  margin-bottom: $xl-size;
}

.widget__message {
  border-bottom: solid 1px
    lighten(
      $color: (
        $light-blue
      ),
      $amount: 10%
    );
  color: $off-white;
  margin: 0;
  padding: $l-size;
  text-align: center;
}

// ? widget header
.widget-header {
  background: $blue;
  color: $off-white;
  padding: $m-size;
  display: flex;
  justify-content: space-between;
}

.widget-header--title {
  margin: 0;
}
