.header {
  background: $off-black;
  color: white;
  margin-bottom: $l-size;
  padding: 1.6rem 0;
}

.header--title {
  font-size: $m-size;
  margin: 0;
}

.header--subtitle {
  color: $off-white;
  font-size: $m-size;
  font-weight: 500;
  margin: 0;
}
@media (min-width: 45rem) {
  .header{
    font-size: $xl-size;
  }
}
