.big-button {
  background: $purple;
  border: none;
  border-radius: 3px;
  border-bottom: 0.6rem solid darken($color: $purple, $amount: 20);
  color: white;
  font-weight: bold;
  font-size: $l-size;
  margin-bottom: $m-size;
  padding: 2.4rem;
  width: 100%;
}

.big-button:disabled {
  opacity: 5;
}
@media (min-width: 45rem) {
  .big-button {
    font-size: $l-size;
    margin-bottom: $xl-size;
  }
}

.button {
  background: $purple;
  border: none;
  border-radius: 3px;
  border-bottom: 0.3rem solid darken($color: $purple, $amount: 10%);
  color: white;
  font-weight: 500;
  padding: $s-size;
}

.button--link {
  background: none;
  border: none;
  color: $off-white;
  padding: 0;
}
