.add-option-error {
  color: $off-white;
  font-style: italic;
  margin: $m-size 0 0 0;
  padding: 0 $m-size;
}

.add-option {
  display: flex;
  padding: $m-size;
  flex-direction: column;
}
.add-option-input {
  background: $dark-blue;
  border: none;
  color: $off-white;
  border-bottom: 0.3rem solid darken($dark-blue, $amount: 10%);
  border-radius: 3px;
  margin-right: $s-size;
  margin: 0 0 $s-size 0;
  flex-grow: 1;
  padding: $s-size;
}
@media (min-width: 45rem) {
  .add-option {
    flex-direction: row;
  }
  .add-option-input {
    margin: 0 $s-size 0 0;
  }
}
